function sort() {
    const sortSearchForCategoryItem = document.querySelectorAll('.sort__search-for__category_item'),
          sortNeardyCategoryItem = document.querySelectorAll('.sort__neardy__category_item');

    function sortCategory(category) {
        category.forEach(item => {
            item.addEventListener('click', () => {
                category.forEach(item => {
                    item.classList.remove('active');
                });
    
                item.classList.add('active');
            });
        });
    }

    sortCategory(sortSearchForCategoryItem);
    sortCategory(sortNeardyCategoryItem);
    const restaurants = document.querySelectorAll('.slider__restaurants_item');
    sortCategory(restaurants);
    
    sortSearchForCategoryItem.forEach(item => {
        let border = item.querySelector('.border'),
            borderWidth = getComputedStyle(border).width.match(/\d+/),
            newBorderWidth = borderWidth * (100 / 1280);
            border.style.width = `${newBorderWidth}vw`;

        let borderHeight = getComputedStyle(border).height.match(/\d+/),
            newBorderHeight = borderHeight * (100 / 1280);
            border.style.height = `${newBorderHeight}vw`;
    });
}

export default sort;